import { Link } from "react-router-dom";
import "antd/dist/antd.css";
import { Breadcrumb } from "antd";

const Navigation = () => {
  return (
    <nav className="navigation">
      <div id="logo">
        <Link
          to="/bored-activities/"
          onClick={() => window.location.to("/bored-activities/")}
        >
          Bored activities
        </Link>
      </div>
      <Breadcrumb>
        <Breadcrumb.Item>
          <Link className="link" to="/bored-activities/">
            Random activity
          </Link>
        </Breadcrumb.Item>
        <Breadcrumb.Item>
          <Link className="link" to="/bored-activities/fav">
            Favourites
          </Link>
        </Breadcrumb.Item>
        <Breadcrumb.Item>
          <Link className="link" to="/bored-activities/about">
            About
          </Link>
        </Breadcrumb.Item>
      </Breadcrumb>
    </nav>
  );
};

export default Navigation;
